import KsImage from '@/components/ks-components/image/Image.vue'
export default {
  name: "KsAmplification",
  props: {
	/**图片地址 */
	src: String,
	index:0,
	imgArr:Array
  },
  data() {
	return {
		baseUrl:this.$store.state.SRC+"/config/app",
	  indicatorDots: false,
	  autoplay: false,
	  interval: 2000,
	  duration: 500,
	  curr:this.index,
	  isShow:false
	};
  },
  components:{
  	KsImage
  },
  methods: {
	isShowImg(){
		this.isShow = true
		this.$emit('isShowStatus',this.isShow)
	},
	close(){
		this.isShow=false
		this.curr = this.index
		this.$emit('isShowStatus',this.isShow)
	},
	setIndex(e){
		this.curr = e.detail.current
	}
  },
  watch: {
	'index'(){
	  this.curr = this.index
	}
  }
};
